@charset "UTF-8";

/* src/components/homepage/homepage.component.scss */
html,
body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
}
header {
  background:
    linear-gradient(
      90deg,
      #7f8fa6,
      #dcdde1);
  color: #2c3e50;
  text-align: center;
  padding: 20px;
}
header h1 {
  margin: 0;
  font-size: 2.5em;
}
header p {
  margin: 10px 0 0;
  font-size: 1.2em;
}
.hero {
  text-align: center;
  padding: 50px 20px;
  background-color: #7fb6e6;
  color: #2c3e50;
  width: 100%;
  box-sizing: border-box;
}
.hero h2 {
  font-size: 2.5em;
}
.hero p {
  font-size: 1.2em;
}
.hero .btn {
  display: inline-block;
  margin: 20px 10px;
  padding: 10px 20px;
  font-size: 1em;
  background: #0078d4;
  color: #fff;
  text-decoration: none;
  border-radius: 5px;
}
.hero .btn:hover {
  background: #005bb5;
}
.products-section {
  padding: 40px 20px;
  text-align: center;
  background-color: #7fb6e6;
  width: 100%;
  box-sizing: border-box;
}
.products-section h3 {
  margin-bottom: 20px;
  font-size: 2em;
  color: #2c3e50;
}
.products {
  display: flex;
  justify-content: center;
  gap: 20px;
  flex-wrap: wrap;
}
.product {
  background: #fff;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  width: 250px;
  text-align: center;
}
.product img {
  max-width: 100%;
  height: 150px;
  object-fit: cover;
  border-radius: 10px;
}
.product h4 {
  font-size: 1.2em;
  margin: 10px 0;
}
.product p {
  font-size: 1em;
  color: #666;
}
.product .btn {
  display: inline-block;
  margin: 10px;
  padding: 10px 20px;
  font-size: 1em;
  background: #28a745;
  color: #fff;
  text-decoration: none;
  border-radius: 5px;
}
.product .btn:hover {
  background: #218838;
}
.about-section {
  padding: 40px 20px;
  text-align: center;
  background-color: #7fb6e6;
  width: 100%;
  box-sizing: border-box;
}
.about-section h3 {
  font-size: 2.5em;
  font-weight: bold;
  color: #2c3e50;
  letter-spacing: 1px;
}
.about-section p {
  font-size: 1.2em;
  color: #666;
}
footer {
  background: #2c3e50;
  color: #fff;
  text-align: center;
  padding: 20px;
  font-size: 0.9em;
  width: 100%;
  box-sizing: border-box;
}
footer a {
  color: #1abc9c;
  text-decoration: none;
}
footer a:hover {
  text-decoration: underline;
}
/*# sourceMappingURL=homepage.component-USALWD3V.css.map */
