@charset "UTF-8";

/* src/components/navbar/navbar.component.scss */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.navbar {
  background-color: #333;
  color: #fff;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 20px;
}
.container {
  display: flex;
  align-items: center;
  width: 100%;
}
.nav-links {
  list-style: none;
  display: flex;
  margin: 0;
  padding: 0;
}
.nav-links li {
  margin-right: 20px;
}
.nav-links a {
  color: #fff;
  text-decoration: none;
  transition: color 0.3s ease;
}
.nav-links a:hover {
  color: #00bcd4;
}
.cart-container {
  margin-left: auto;
  display: flex;
  align-items: center;
}
.cart-text {
  color: white;
  font-size: 16px;
  margin-right: 8px;
  display: inline-block;
  vertical-align: middle;
}
.cart-icon {
  width: 35px;
  height: 35px;
  cursor: pointer;
  transition: transform 0.2s ease-in-out;
  vertical-align: middle;
}
.cart-icon:hover {
  transform: scale(1.1);
}
