@charset "UTF-8";

/* src/components/produse/produse.component.scss */
body {
  font-family: Arial, sans-serif;
  background-color: #f5f5f5;
  margin: 0;
  padding: 0;
}
.product-container {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  padding: 20px;
  justify-content: center;
}
.product-card {
  background: #fff;
  border-radius: 10px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  padding: 15px;
  width: 220px;
  text-align: center;
  transition: transform 0.3s, box-shadow 0.3s;
}
.product-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
}
.product-img {
  width: 100%;
  height: 130px;
  object-fit: cover;
  border-radius: 8px;
}
.product-details h3 {
  margin: 10px 0 5px;
  color: #333;
  font-size: 1.1rem;
}
.product-details .price {
  font-size: 1rem;
  font-weight: bold;
  color: #27ae60;
  margin-bottom: 5px;
}
.product-details .description {
  font-size: 0.9rem;
  color: #555;
  white-space: normal;
}
.add-to-cart-btn {
  background-color: #4CAF50;
  color: white;
  padding: 10px 20px;
  border: none;
  cursor: pointer;
  font-size: 16px;
  border-radius: 5px;
  margin-top: 10px;
}
.add-to-cart-btn:hover {
  background-color: #45a049;
}
