/* src/components/thank-you/thank-you.component.scss */
.thank-you-container {
  max-width: 800px;
  margin: 0 auto;
  padding: 40px;
  font-family: Arial, sans-serif;
  background-color: #f8f8f8;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  text-align: center;
}
h2 {
  font-size: 28px;
  color: #333;
  margin-bottom: 20px;
}
.thank-you-message {
  font-size: 18px;
  color: #555;
  margin-bottom: 30px;
}
.thank-you-actions {
  display: flex;
  justify-content: center;
  gap: 20px;
}
.continue-shopping-btn,
.view-order-btn {
  background-color: #4CAF50;
  color: white;
  padding: 12px 24px;
  border: none;
  cursor: pointer;
  font-size: 16px;
  border-radius: 5px;
  transition: background-color 0.3s ease;
}
.continue-shopping-btn:hover,
.view-order-btn:hover {
  background-color: #45a049;
}
.view-order-btn {
  background-color: #008CBA;
}
.view-order-btn:hover {
  background-color: #007b8b;
}
